import React from "react";
import rogerio from "./rogerio.jpg";
import "./index.css";

export default (props) => (
  <div className="Header part">
    <div className="Header-image">
      <img src={rogerio} alt="Rogerio's photo" />
    </div>
    <h1>ROGÉRIO FERNANDES</h1>
  </div>
);



